<template>
	<div class="one">
		<van-pull-refresh v-model="isLoading" success-text="刷新成功" @refresh="onRefresh">
			<!-- <p>刷新次数: {{ count }}</p> -->
		</van-pull-refresh>
	</div>
</template>

<script>
	import { Toast } from 'vant';
	export default {
	  data() {
	    return {
	      count: 0,
	      isLoading: false,
	    };
	  },
	  methods: {
	    onRefresh() {
	      setTimeout(() => {
	        this.isLoading = false;
	        this.count++;
	      }, 1000);
	    },
	  },
	};
</script>

<style scoped>
	.one{
		height: 100%;
	}
</style>
